import React, {Component} from 'react';
import {Card,CardBody,CardHeader,CardImg, CardTitle, CardText, CardSubtitle,Breadcrumb,BreadcrumbItem,Button,Modal,Label,Row,Col, ModalHeader, ModalBody} from 'reactstrap';
import {Link} from 'react-router-dom';
import {LocalForm,Errors,Control} from 'react-redux-form';
import {Loading} from './LoadingComponent';
import {baseUrl} from './shared/baseUrl';
import { FadeTransform, Fade, Stagger } from 'react-animation-components';

const minLength=(len)=>(val)=> val && val.length>=len;
const maxLength=(len)=>(val)=> !val || val.length<=len;

function RenderComments({comments}){
    const commentsToRender=comments.map(comment=>{
        return (
            <Fade in>
                <li key={comment.id}>
                    <p>{comment.comment}</p>
                    <p>-- {comment.author} , {new Intl.DateTimeFormat('en-US', { year: 'numeric', month: 'short', day: '2-digit'}).format(new Date(Date.parse(comment.date)))}</p>
                </li>
            </Fade>
        );
    });
    return commentsToRender;
}

function RenderDish({dish}){
    return (
        <div className="col-12 col-md-5 m-1">
        <FadeTransform in transformProps={{exitTransform: 'scale(0.5) translateY(-50%)'}}>
            <Card>
                <CardImg src={baseUrl+dish.image} alt={dish.name}></CardImg>
                    <CardBody>
                        <CardTitle>
                            <h3>{dish.name}</h3>
                        </CardTitle>
                        <CardText>
                            {dish.description}
                        </CardText>
                    </CardBody>
            </Card>
        </FadeTransform>
        </div>
    );
}

function DishDetail(props){
        if(props.isLoading){
        return(
            <div className="container">
                <div className="row">
                    <Loading />
                </div>
            </div>
        );
        }   
        else if(props.errMess){
            return(
                <div className="container">
                    <div className="row">
                        <h3 className="text-danger">{props.errMess}</h3>
                    </div>
                </div>
            );
        }
        else if(props.dish!==null){            
            return (
                <div className="container">
                    <div className="row">
                        <Breadcrumb>
                            <BreadcrumbItem><Link to="/menu">Menu</Link></BreadcrumbItem>
                            <BreadcrumbItem active>{props.dish.name}</BreadcrumbItem>
                        </Breadcrumb>
                    </div>
                    <div className="row justify-content-center">
                        <RenderDish dish={props.dish} />
                        <div className="col-12 col-md-5 m-1">
                            <h2 className="text-center">Comments</h2>
                                <Stagger in>
                                    <ul className="list-unstyled">
                                    <RenderComments dishId={props.dish.id} comments={props.comments} postComment={props.postComment}/>
                                    </ul>
                                </Stagger>
                            <CommentForm dishId={props.dish.id} postComment={props.postComment}/>
                        </div>
                    </div>
                </div>
            );
        }
        else{
            return null;
        }
}

class CommentForm extends Component{
    constructor(props){
        super(props);
        this.state={
            isModalOpen: false
        }
    }

    toggelModal=()=>{
        this.setState({isModalOpen: !this.state.isModalOpen});
    }

    handleSubmit=(values)=>{
        this.props.postComment(this.props.dishId,values.rating,values.name,values.comment);
        this.toggelModal();
    }

    render(){
        return(
            <React.Fragment>
                <div className="d-flex mb-5 mt-2 justify-content-start">
                    <Button type="button" className="btn font-weight-bold btn-secondary" onClick={this.toggelModal}>
                        <span className="fa fa-pencil"></span> Submit Comment
                    </Button>
                </div>
                <div>
                    <Modal fade isOpen={this.state.isModalOpen} toggle={this.toggelModal}>
                        <ModalHeader charcode="\u0e49" className="bg-dark2 text-white" toggle={this.toggelModal}>
                            Comment
                        </ModalHeader>
                        <ModalBody className="p-4">
                            <LocalForm onSubmit={this.handleSubmit}>
                                <Row className="form-group">
                                    <Label className="col-12" htmlFor="rating">Rating</Label>
                                    <Col sm={12}>
                                        <Control.select defaultValue="1" className="form-control" model=".rating"
                                        id="rating" name="rating">
                                            <option value="1">1</option>
                                            <option value="2">2</option>
                                            <option value="3">3</option>
                                            <option value="4">4</option>
                                            <option value="5">5</option>
                                        </Control.select>
                                    </Col>
                                </Row>
                                <Row className="form-group">
                                    <Label className="col-12" htmlFor="name">Your Name</Label>
                                    <Col sm={12}>
                                        <Control.text className="form-control" model=".name"
                                        id="name" name="name"
                                        validators={{minLength:minLength(3),maxLength: maxLength(15)}}>
                                        </Control.text>
                                        <Errors
                                        className="text-danger"
                                        show="touched"
                                        model=".name"
                                        messages={{
                                            minLength: "Name must be atleast 3 letters long.",
                                            maxLength: "Name must not exceed 15 characters."
                                        }}>

                                        </Errors>
                                    </Col>
                                </Row>
                                <Row className="form-group">
                                    <Label className="col-12" htmlFor="comment">Comment</Label>
                                    <Col sm={12}>
                                        <Control.textarea className="form-control" model=".comment"
                                        id="comment" name="comment"
                                        rows="6">
                                        </Control.textarea>
                                    </Col>
                                </Row>
                                <Row className="form-group justify-content-center">
                                    <Button type="submit" className="btn bg-primary">Submit</Button>
                                </Row>
                            </LocalForm>
                        </ModalBody>
                    </Modal>
                </div>
            </React.Fragment>
        );
    }
}

export default DishDetail;