import React from 'react';
import {Card,CardBody,CardImg,CardTitle,CardSubtitle, CardText, Breadcrumb,BreadcrumbItem} from 'reactstrap';
import {Loading} from './LoadingComponent';
import {baseUrl} from './shared/baseUrl';
import {FadeTransform} from 'react-animation-components';

function RenderCard({item,isLoading,errMess}){
    if(isLoading){
        return(
            <div className="container">
                <div className="row">
                    <Loading />
                </div>
            </div>
        );
    }   
    else if(errMess){
        return(
            <div className="container">
                <div className="row">
                    <h3 className="text-danger">{errMess}</h3>
                </div>
            </div>
        );
    }        
    else{
        return(
            <FadeTransform  in
            transformProps={{
                exitTransform: 'scale(0.5) translateY(-50%)'
            }}>
                <Card>
                    <CardImg src={baseUrl+item.image} alt={item.name} />
                    <CardBody>
                    <CardTitle><h3>{item.name}</h3></CardTitle>
                    {item.designation ? <CardSubtitle><h5>{item.designation}</h5></CardSubtitle> : null }
                    <CardText>{item.description}</CardText>
                    </CardBody>
                </Card>
            </FadeTransform>
        );
    }
}

function Home(props){
    return(
        <div className="container">
            <div className="row">
                <Breadcrumb>
                    <BreadcrumbItem active>Home</BreadcrumbItem>
                </Breadcrumb>
                <div className="col-12 text-center">
                    <h2>Home</h2>
                    <hr/>
                </div>
            </div>
            <div className="row align-items-start row-content">
                <div className="col-12 col-md m-1">
                    <RenderCard item={props.dish}
                        isLoading={props.dishesLoading}
                        errMess={props.dishesErrMess}
                    />
                </div>
                <div className="col-12 col-md m-1">
                    <RenderCard item={props.promotion}
                        isLoading={props.promotionsLoading}
                        errMess={props.promotionsErrMess}                            
                        />
                </div>
                <div className="col-12 col-md m-1">
                    <RenderCard item={props.leader}
                        isLoading={props.leadersLoading}
                        errMess={props.leadersErrMess}
                    />
                </div>
            </div>
        </div>
    );
}

export default Home;