import React, { Component } from 'react';
import { Navbar,NavItem,NavbarToggler,Collapse,Nav, NavbarBrand, Jumbotron, Modal, ModalHeader, ModalBody, Form, FormGroup, Input, Label, Button } from 'reactstrap';
import {NavLink} from 'react-router-dom';

class Header extends Component {
  constructor(props){
    super(props);
    this.state={
      isNavOpen:false,
      isModalOpen:false
    };
    this.toggleNav=this.toggleNav.bind(this);
    this.toggleModal=this.toggleModal.bind(this);
    this.handleLogin=this.handleLogin.bind(this);
  }

  toggleNav(){
    this.setState({isNavOpen: !this.state.isNavOpen});
  }

  toggleModal(){
    this.setState({isModalOpen: !this.state.isModalOpen})
  }

  handleLogin(event){
    this.toggleModal();
    alert(JSON.stringify({
      Username: this.username.value,
      Password: this.password.value,
      Remember: this.remember.checked
    }));
    event.preventDefault();
  }

  render() {
    return(
    <React.Fragment>
      <Navbar dark expand="md">
        <div className="container">
          <NavbarBrand href="/">
            <img src="assets/images/logo.png" alt="Ristorante Confusion" height="30" width="41"></img>
          </NavbarBrand>
        <NavbarToggler onClick={this.toggleNav}></NavbarToggler>
          <Collapse isOpen={this.state.isNavOpen} navbar>
            <Nav navbar className="ml-auto">
              <NavItem>
                <NavLink className="nav-link" to="/home" >
                  <span className="fa m-1 fa-home fa-lg"></span>Home
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink className="nav-link" to="/aboutus">
                  <span className="fa m-1 fa-info fa-lg"></span>About Us
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink className="nav-link" to="/menu">
                  <span className="fa m-1 fa-list fa-lg"></span>Menu
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink className="nav-link" to="/contactus">
                  <span className="fa m-1 fa-address-card fa-lg"></span>Contact Us
                </NavLink>
              </NavItem>
              <NavItem>
                <Button outline onClick={this.toggleModal}>
                  <span className="fa fa-sign-in fa-lg"></span> Login
                </Button>
              </NavItem>
            </Nav>
          </Collapse>
        </div>
      </Navbar>
      <Jumbotron>
           <div className="container">
               <div className="row row-header">
                   <div className="col-12 col-sm-6">
                       <h1>Ristorante con Fusion</h1>
                       <p>We take inspiration from the World's best cuisines, and create a unique fusion experience. Our lipsmacking creations will tickle your culinary senses!</p>
                   </div>
               </div>
           </div>
       </Jumbotron>
       <Modal fade={true} isOpen={this.state.isModalOpen} toggle={this.toggleModal}>
         <ModalHeader className="bg-dark2 text-white" toggle={this.toggleModal}>Log In</ModalHeader>
         <ModalBody className="p-4">
           <Form>
             <FormGroup row>
               <Label htmlFor="username">Username</Label>
               <Input type="text" id="username" name="username"
               innerRef={(input)=>this.username=input}></Input>
             </FormGroup>
             <FormGroup row>
               <Label htmlFor="password">Password</Label>
               <Input type="password" id="password" name="password"
               innerRef={(input)=>this.password=input}></Input>
             </FormGroup>
             <FormGroup row>
               <FormGroup check>
                  <Label check>
                    <Input type="checkbox" id="remember" name="remember"
                    innerRef={(input)=>this.remember=input}></Input>
                    Remember me
                  </Label>
               </FormGroup>
             </FormGroup>
             <FormGroup row className="justify-content-around">
              <Button type="submit" onClick={this.handleLogin} className="btn bg-success">Log In</Button>
              <Button type="button" onClick={this.toggleModal} className="btn bg-primary">Cancel</Button>
             </FormGroup>
           </Form>
         </ModalBody>
       </Modal>
    </React.Fragment>
    );
  }
}

export default Header;