import React from 'react';
//import {Media} from 'reactstrap';
import {Card,CardBody,CardImg,CardImgOverlay,CardText,CardTitle,UncontrolledCollapse, CardHeader, Breadcrumb, BreadcrumbItem} from 'reactstrap';
import {Link} from 'react-router-dom';
import {Loading} from './LoadingComponent';
import {baseUrl} from './shared/baseUrl';

function RenderMenu({dishes}){
    const menu=dishes.map(dish=>{
        return (
            // <div id={dish.id} className="col-12 mt-5">
            //     <Media tag="li" className="rd">
            //         <Media left middle>
            //             <Media object src={dish.image} alt={dish.name} />
            //         </Media>
            //         <Media body className="ml-5">
            //             <Media heading>{dish.name}</Media>
            //             <p>{dish.description}</p>
            //             <p>{dish.description}</p>
            //             <p>{dish.description}</p>
            //             <p>{dish.description}</p>
            //             <p>{dish.description}</p>
            //         </Media>
            //     </Media>
            // </div>
            <div className="col-12 col-md-5 m-1 text-center">
                <RenderDish dish={dish} />
            </div>
        );
        });
    return menu;
}

function RenderDish({dish}){
   return (
            <Card className="bg-light bg-primary"  style={{cursor:"pointer"}}>
                <Link to={`/menu/${dish.id}`}>
                    <CardImg src={baseUrl+dish.image} alt={dish.name}>

                    </CardImg>
                    <CardHeader>
                        <CardTitle>
                            <h3>{dish.name}</h3>
                        </CardTitle>                            
                    </CardHeader>
                </Link>
                {/* <UncontrolledCollapse toggler={`#toggle${dish.id}`}>
                <CardBody>
                    <CardText>
                        {dish.description}
                    </CardText>
                </CardBody>
                </UncontrolledCollapse> */}
            </Card>
   );
}

function Menu({dishes,isLoading,errMess}){
    if(isLoading){
        return(
            <div className="container">
                <div className="row">
                    <Loading />
                </div>
            </div>
        );
    }   
    else if(errMess){
        return (
            <div className="container">
                <div className="row">
                    <h3 className="text-danger">{errMess}</h3>
                </div>
            </div>
        );
    }        
    else{
        return (
            <div className="container">
                <div className="row">
                    <Breadcrumb>
                        <BreadcrumbItem><Link to="/home">Home</Link></BreadcrumbItem>
                        <BreadcrumbItem active>Menu</BreadcrumbItem>
                    </Breadcrumb>
                    <div className="col-12 text-center">
                        <h2>Menu</h2>
                        <hr/>
                    </div>
                </div>
                <div className="row justify-content-center">
                    <RenderMenu dishes={dishes} />
                </div>
            </div>
        );
    }
}


export default Menu;