export const postFeedback=(feed)=>(dispatch)=>{
    const feedback={...feed};
    feedback.date= new Date().toISOString();

    return fetch(baseUrl+"feedback",{
        method: "POST",
        body: JSON.stringify(feedback),
        headers: {
            'Content-Type':'application/json'
        },
        credentials: "same-origin"
    }).then(res=>{
        if(res.ok){
            return res;
        }
        else{
            var err=new Error("Error "+res.status+": "+res.statusText);
            throw err;            
        }
    },error=>{
        var err=new Error(error.message);
        throw err;
    }).then(res=>res.json()).then(res=>{
        dispatch(feedbackPosted(res));
        alert(JSON.stringify(res));
    }).catch(err=>{
        dispatch(feedbackFailed(err));
        alert("Your feedback could not be posted. "+err.message);
    })
}

export const feedbackPosted=(feedback)=>{
    return ({
        type: ActionTypes.FEEDBACK_POSTED,
        payload: feedback
    });
}

export const feedbackFailed=(errMess)=>{
    return ({
        type: ActionTypes.LEADERS_FAILED,
        payload: errMess
    });
}