import React, {Component} from 'react';
import Menu from './MenuComponent';
import DishDetail from './DishdetailComponent';
import Header from './HeaderComponent';
import Footer from './FooterComponent';
import Contact from './ContactComponent';
import {Route, Switch, Redirect, withRouter} from 'react-router-dom';
import Home from './HomeComponent';
import AboutUs from './AboutComponent';
import {connect} from 'react-redux';
import {postComment, fetchDishes,fetchComments, fetchLeaders, fetchPromos,postFeedback} from '../redux/ActionCreator';
import {actions} from 'react-redux-form';
import {TransitionGroup,CSSTransition} from 'react-transition-group';

const mapStateToProps=(state)=>{
    return ({
        dishes: state.dishes,
        leaders: state.leaders,
        promotions: state.promotions,
        comments: state.comments
    })
}

const mapDispatchToProps=(dispatch)=>{
    return({
        postComment: (dishId,rating,author,comment)=> dispatch(postComment(dishId,rating,author,comment)),
        fetchDishes: ()=> dispatch(fetchDishes()),
        resetFeedback: ()=> dispatch(actions.reset('feedback')),
        fetchComments: ()=> dispatch(fetchComments()),
        fetchLeaders: ()=> dispatch(fetchLeaders()),
        fetchPromos: ()=> dispatch(fetchPromos()),
        postFeedback: (feedback)=>dispatch(postFeedback(feedback))
    })
}

class Main extends Component {

    constructor(props){
        super(props);
        this.HomePage=this.HomePage.bind(this);
    }
    
    HomePage(){
        return (<Home 
            dish={this.props.dishes.dishes.filter((dish)=> dish.featured)[0]}
            dishesLoading={this.props.dishes.isLoading}
            dishesErrMess={this.props.dishes.errMess}
            leader={this.props.leaders.leaders.filter((leader)=>leader.featured)[0]}
            leadersLoading={this.props.leaders.isLoading}
            leadersErrMess={this.props.leaders.errMess}
            promotion={this.props.promotions.promotions.filter((promo)=>promo.featured)[0]}
            promotionsLoading={this.props.promotions.isLoading}
            promotionsErrMess={this.props.promotions.errMess}
        />);
    };

    componentDidMount(){
        this.props.fetchDishes();
        this.props.fetchComments();
        this.props.fetchPromos();
        this.props.fetchLeaders();
    }

    DishWithId=({match})=>{
        return (
            <DishDetail dish={this.props.dishes.dishes.filter(dish=>dish.id===parseInt(match.params.id,10))[0]} 
                comments={this.props.comments.comments.filter(comment=>comment.dishId===parseInt(match.params.id,10))}
                isLoading={this.props.dishes.isLoading}
                errMess={this.props.dishes.errMess}
                commentsErrMess={this.props.comments.errMess}
                postComment={this.props.postComment}
            />
        );
    }

    render(){
        return (
        <div>
            <Header />
            <TransitionGroup>
                <CSSTransition key={this.props.location.key} classNames="page" timeout={300}>
                    <Switch location={this.props.location}>
                        <Route path="/home" component={this.HomePage} ></Route>
                        <Route path="/contactus" component={()=><Contact resetFeedback={this.props.resetFeedback}
                            postFeedback={this.props.postFeedback}
                        />}></Route>
                        <Route exact path="/menu" component={()=><Menu dishes={this.props.dishes.dishes}
                            isLoading={this.props.dishes.isLoading}
                            errMess={this.props.dishes.errMess}/>}>                        
                        </Route>
                        <Route path="/menu/:id" component={this.DishWithId}></Route>
                        <Route path="/aboutus" component={()=> <AboutUs leaders={this.props.leaders.leaders} 
                            isLoading={this.props.leaders.isLoading}
                            errMess={this.props.leaders.errMess}
                            />}>                    
                        </Route>
                        <Redirect to="/home"></Redirect>
                    </Switch>
                </CSSTransition>
            </TransitionGroup>
            <Footer />
        </div>
        );
    }
}

export default withRouter(connect(mapStateToProps,mapDispatchToProps)(Main));
